import { VueConstructor } from 'vue';
import xhr, { XHRStatic } from './xhr';

const VueXhrPlugin = {
  install: (Vue: VueConstructor) => {
    Object.defineProperties(Vue.prototype, {
      $xhr: {
        get() {
          return xhr;
        },
      },
    });
  },
};

declare module 'vue/types/vue' {
  interface Vue {
    $xhr: XHRStatic;
  }
}

export default VueXhrPlugin;
