











































import { Component, Vue } from 'vue-property-decorator';
import Submit from '../components/Submit.vue';

@Component({
  name: 'Home',
  components: {
    Submit,
  },
})
export default class Home extends Vue {
  phone = '';

  name = '';

  company = '';

  submiting = false;

  validatorPhone(val: string) {
    console.log(234234);
    return /^(?:(?:\+|00)86)?1\d{10}$/.test(val);
  }

  validatorCaptcha(val: string) {
    return /^\d{1,}$/.test(val);
  }

  async onSubmit() {
    console.log('__LYG_JAX', this.phone, this.name, this.company); // debug-log
    this.submiting = true;
    try {
      const data = await this.$xhr.api.post('/huayang/users', {
        params: {
          phone: this.phone,
          name: this.name,
          company: this.company,
          awardType: this.$route.query.type,
        },
      });
      this.submiting = false;
      localStorage.setItem('award', JSON.stringify(data));
      this.$router.replace({ name: 'success', query: { type: this.$route.query.type } });
    } catch (error) {
      this.submiting = false;
      if (error.code == -9) {
        this.$router.replace({ name: 'failure', query: { type: this.$route.query.type } });
        return;
      }
      this.$toast(error && error.msg || '网络不给力');
    }

    // todo submit
  }

  onFormFailed(errorInfo: any) {
    const { errors } = errorInfo;
    const validFied = errors[0];
    this.$toast(validFied.message);
  }
}
