import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';
import qs from 'qs';

interface Methods {
  get: (url: string, config?: AxiosRequestConfig) => any;
  post: (url: string, config?: AxiosRequestConfig) => any;
}

export interface XHRStatic {
  api: Methods;
  apiCDN: Methods;
}

class XHRService {
  api: Methods;

  apiCDN: Methods;

  private axiosRef: AxiosInstance;

  constructor() {
    this.axiosRef = axios.create({
      timeout: 10000,
      transformRequest: (data, headers) => {
        if (headers.post['Content-Type'].indexOf('application/x-www-form-urlencoded') != -1) {
          return qs.stringify(data);
        }
        return JSON.stringify(data);
      },
    });

    this.registerRequestInterceptor();
    this.registerResponseInterceptor();

    this.api = this.transformRequestMethods(this.apiBaseURL());
    this.apiCDN = this.transformRequestMethods(this.apiBaseURL(true));
  }

  apiBaseURL(isCDN?: boolean) {
    // return '//www.shinejoy.top/static/senhuohui/';
    // return 'http://localhost:3000';
    if (process.env.NODE_ENV === 'production') {
      return 'http://www.shinejoy.top/';
    }
    return 'http://localhost:3000';
  }

  transformRequestMethods(baseURL: string) {
    const { axiosRef } = this;

    return {
      get(url: string, config?: AxiosRequestConfig) {
        return axiosRef.get(url, { ...config, baseURL });
      },
      post(url: string, config?: AxiosRequestConfig) {
        return axiosRef.request({
          baseURL,
          url,
          ...config,
          method: 'POST',
        });
      },
      postJSON(url: string, config?: AxiosRequestConfig) {
        const headers = {
          post: {
            'Content-Type': 'application/json',
          },
        };

        return this.post(url, {
          ...config,
          headers,
        });
      },
    };
  }

  registerRequestInterceptor() {
    this.axiosRef.interceptors.request.use(
      (config: AxiosRequestConfig): AxiosRequestConfig => {
        const { params, method = 'GET' } = config;

        if (method.toUpperCase() != 'GET') {
          config.data = params;
          config.params = {};
        }

        return config;
      },
      (error: any) => {
        console.log(error);
        Promise.reject(error);
      },
    );
  }

  registerResponseInterceptor() {
    this.axiosRef.interceptors.response.use(
      (response: AxiosResponse): any => {
        const { data: rsData } = response;

        const {
          code, data, message,
        } = rsData;

        if (code == 200 || code == 0) {
          return data;
        }

        return Promise.reject(rsData);
      },
      (error: any) => {
        const responseError: any = {
          msg: '网络不给力',
          statusCode: -999,
        };
        return Promise.reject(responseError);
      },
    );
  }
}

const xhrInstance: XHRStatic = new XHRService();

export { XHRService };

export default xhrInstance;
